<template>
  <view class="work-container">
	<uni-section v-if :title="id ? '项目信息' : '项目新增'" type="line">
		<view>
			<uni-forms ref="form" :modelValue="form" label-position="top">
				<uni-forms-item label="项目名称" required>
					<uni-easyinput trim="all" v-model="form.projectName" placeholder="请输入项目名称" :disabled="flag == 0"/>
				</uni-forms-item>
				<uni-forms-item label="委托单位" required>
					<uni-easyinput trim="all" v-model="form.clientUnit" placeholder="请输入委托单位" :disabled="flag == 0"/>
				</uni-forms-item>
				<uni-forms-item label="年份" required>
					<uni-data-select
						v-model="form.yearId"
						:localdata="yearOptions"
						:clear="false"
					  ></uni-data-select>
				</uni-forms-item>
				<uni-forms-item label="区域" required>
					<uni-data-select
						v-model="form.region"
						:localdata="regionOptions"
						:clear="false"
					  ></uni-data-select>
				</uni-forms-item>
				<uni-forms-item label="委托日期" required>
					<uni-datetime-picker type="date" v-model="form.entrustDate" :disabled="flag == 0"/>
				</uni-forms-item>
				<uni-forms-item label="项目地址">
					<uni-easyinput type="textarea" trim="all" v-model="form.projectAddr" placeholder="请输入项目地址" :disabled="flag == 0"/>
				</uni-forms-item>
				<uni-forms-item label="备注">
					<uni-easyinput type="textarea" autoHeight  trim="all" v-model="form.remark" placeholder="请输入备注" :disabled="flag == 0"/>
				</uni-forms-item>
			</uni-forms>
		</view>
		<view class="button-group">
			<button v-if="id && checkPermi(['business:project:remove'])"  style="color: #ffffff; background-color: #ff0000;"  size="mini" @click="del">删除</button>
		    <button v-else style="color: #000000; background-color: #ffffff;" size="mini" @click="goBack()">返回</button>
			<button type="primary" v-if="flag!=0"  size="mini" @click="submit">提交</button>
		</view>
		<!-- 提示信息弹窗 -->
		<uni-popup ref="message" type="message">
			<uni-popup-message :type="msgType" :message="messageText" :duration="2000"></uni-popup-message>
		</uni-popup>
		<!-- 提示窗 -->
		<uni-popup ref="alertDialog" type="dialog">
			<uni-popup-dialog :type="alertType" cancelText="取消" confirmText="确认" title="提示" :content="alertContent" @confirm="dialogConfirm"
				@close="dialogClose"></uni-popup-dialog>
		</uni-popup>
	</uni-section>
  </view>
</template>

<script>
import {getProject,addProject,updateProject } from "@/api/work/business/project"
import {getTaskList} from '@/api/work/archive/task';
export default {
  data () {
    return {
		id:undefined,
		flag:'0',
		nowDay: Number(new Date()),
		styles: {
			color: '#2979FF',
			borderColor: '#2979FF',
			disableColor: '#ffffff'
		},
		// 表单参数
		form: {},
		// 字典项
		yearOptions:[],
		regionOptions:[],
		
		// 消息提示窗口、提示框
		msgType: '',
		messageText: '',
		// 弹出框
		alertType:'',
		alertContent:'',
	}
  },
  onReady() {
  	this.getDicts("bs_project_year").then(response => {
  	  this.yearOptions = response.data;
  	});
	this.getDicts("bs_project_region").then(response => {
	  this.regionOptions = response.data;
	});
  },
  onLoad (params) {
    this.id = params.id
	this.flag = params.flag
    this.loadData()
  },
  methods: {
	// 表单重置
	reset() {
	  this.form = {};
	},
    loadData () {
		const app = this
		if (this.id) {
			const app = this
			getProject(this.id).then(res => {
			  app.form = res.data
			})
		}
    },
    del () {
		this.msgType = 'error'
		this.messageText = '请联系管理员进行删除！'
		this.$refs.message.open()
    },
	submit () {
		const app = this
		this.$refs.form.validate().then(res => {
			if (app.id) {
			  updateProject(app.form).then(res => {
				uni.showToast({ title: '修改成功！' })
				app.reset();
				uni.navigateBack({ delta: 1});
			  })
			} else {
			  addProject(app.form).then(res => {
				uni.showToast({ title: '提交成功！' })
				app.reset();
				uni.navigateBack({ delta: 1});
			  })
			}
		});
	},
    
    goBack () {
      uni.navigateBack({ delta: 1});
    },
  }
}
</script>

<style lang="scss">
.button-group {
	margin-top: 15px;
	display: flex;
	justify-content: space-around;
}

.form-item {
	display: flex;
	align-items: center;
}

.button {
	display: flex;
	align-items: center;
	height: 20px;
	margin-left: 10px;
}
</style>